






















































































































































































































































/*bookmore组件样式开始*/
.row {
    padding: 16px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.row .row-fl {
    float: left;
}
.tit {
    height: 18px;
    line-height: 18px;
    padding: 16px;
}
.tit span {
    height: 18px;
    width: 99%;
    font-size: 18px;
}
.tit a {
    display: block;
    font-size: 14px;
    color: rgb(63, 189, 82);
    float: right;
}
.banner {

    padding: 10px;
    height: 190px;
    overflow-x: auto;
}





/*影评样式*/
.cinecism {
    font-size: 12px;
    color: #9B9B9B;
    line-height: 1.5em;
}
.strong-block {
    display: block;
}




/*----bananer组件----*/
.banner .banner-item {
    width: 10000px;
}
.banner .item {
    /*background: red;*/
    width: 110px;
    /*text-align: center;*/
    float: left;
}
.banner img {
    height: 134px;
}
.text {
    font-size: 12px;
    color: #999;
}
.banner2 {
    height: 190px;
}
.banner2 img {
    width: 100%;
}
.man {
    margin-left: 20px;
    line-height: 40px;
}
.banner i {
    font-style: normal
}



