
.box {
    padding: 16px;
    overflow-y: hidden;
    overflow-x: scroll;
}
.boxs {
    width: 760px;

    overflow: hidden;
}
.boxs div {
    float: left;
    width: 180px;
    height: 180px;
    margin-right: 10px;
    border-radius: 5px;
    text-align: center;
}
.boxs div h3 {

    color: #fff;
    margin-top: 60px;
}
.boxs div .txt {
    height: 20px;
    line-height: 20px;
    font-size: 12px;
    color: #999;
}
.boxs .box1 {
    background: red;
}
.boxs .box2 {
    background: blue;
}
.boxs .box3 {
    background: yellowgreen;
}
.boxs .box4 {
    background: cadetblue;
}



