
.tab {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding: 10px;
    margin: 10px;
}
.tab1,
.tab3 {
    -webkit-box-flex: 2;
        -ms-flex: 2;
            flex: 2;
}
.tab2 {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    text-align: center;
    padding: 10px;
}
.tab2 .book {
    color: #525252;
}
div.Line {
    padding: 20px 0;
    color: #e3e3e3;
}



