.tit {
    height: 18px;
    line-height: 18px;
    padding: 16px;
}
.tit span {
    height: 18px;
    width: 99%;
    font-size: 18px;
}
.tit a {
    display: block;
    font-size: 14px;
    color: rgb(63, 189, 82);
    float: right;
}
.banner {
    padding: 10px;
    height: 190px;
    overflow-x: auto;
}
.names {
    font-size: 12px;
    color: #9B9B9B;
    line-height: 1.5em;
}
.wxjg,
.xsjg {
    font-size: 15px;
}
.wxjg {

    color: #9B9B9B;
}
.xsjg {
    color: red;
}








































/*----bananer组件----*/
.text {
    font-size: 12px;
    color: #999;
}







































































/*::-webkit-scrollbar {
    width: 0px
}*/
.banner .banner-item {
    width: 10000px;
}
.banner .item {
    /*background: red;*/
    width: 110px;
    float: left;
}
.banner img {
    height: 134px;
}
.banner2 {
    height: 190px;
}
.banner2 img {
    width: 100%;
}
.footer-text {
    padding-top: 10px;
    margin-left: 20px;
}



































































