










































































































































































































































/*bookmore组件样式*/
.more-span-txt[data-v-49f8b2ca] {
    height: 18px;
    width: 99%;
    font-size: 18px;
}
.more-right-a[data-v-49f8b2ca] {
    position: absolute;
    right: 0;
    margin-right: 10px;
    font-size: 14px;
    color: rgb(63, 189, 82);
}
.music-name[data-v-49f8b2ca] {
    font-size: 18px;
    color: #e3e3e3;
    margin: 0;
    margin-left: -8px;
}
.titles[data-v-49f8b2ca] {
    font-size: 21px;
    color: #000;
}
.Trend-bottom[data-v-49f8b2ca] {
    border-bottom: 1px solid #f0f1ec;
    padding: 16px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.Trend-bottom .Trend[data-v-49f8b2ca] {
    display: inline-block;
    border: 1px solid #3fbd52;
    height: 26px;
    line-height: 26px;
    border-radius: 20px;
    color: #3fbd52;
    padding: 0 10px;
    margin-right: 10px;
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    margin-bottom: 20px;
}






/*=======*/
.slot-txt[data-v-49f8b2ca] {
    font-size: 15px;
    color: #999;
}







/*====================*/
.banner[data-v-49f8b2ca] {
    padding: 10px;
    overflow-x: scroll;
    overflow-y: hidden;
}
.banner .banner-item[data-v-49f8b2ca] {
    width: 10000px;
    overflow: hidden;
}
.banner strong[data-v-49f8b2ca] {
    font-size: 15px;
    margin-right: 14px;
}
.banner .item[data-v-49f8b2ca] {
    width: 123px;
    /*text-align: center;*/
    float: left;
}
.banner .times[data-v-49f8b2ca],
time .look[data-v-49f8b2ca] {
    font-size: 15px;
    color: #999;
}
.banner img[data-v-49f8b2ca] {
    height: 110px;
}



